* ul,
* li {
    list-style: none;
}

:root {
    --CorText: rgb(201, 129, 216);

    --Cor: #985803;
    --Cor_Shadow: #452803;
    --CorNav: rgba(94, 12, 110, 0.4);
    --CorNav_Shadow: #360340;

    --CorUl: rgba(143, 33, 0, 0.8);

}

header {
    position: fixed;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;

    width: 100%;
    height: 80px;


    &>div:nth-child(1) {
        background-color: var(--Cor);
        box-shadow: 4px 2px 2px var(--Cor_Shadow);


        z-index: 20;
    }

    &>div:nth-child(2) {
        height: auto;
        width: 100%;


        background-color: var(--CorNav);
        box-shadow: 4px 2px 2px var(--CorNav_Shadow);

        z-index: 10;
        backdrop-filter: blur(4px);
        opacity: .9;
    }

}

.AsideNav {
    width: 100%;

    &>nav {
        &>div {
            max-height: 40px;
        }

        ul {
            overflow-x: scroll;


            background-color: var(--CorUl);
            scrollbar-darkshadow-color: var(--Cor_Shadow);
            scrollbar-color: rgb(77, 47, 1) rgba(199, 113, 1, 0.42);

            &>li {
                background-color: black;
                margin: 5px 40px;

                &, &>button, &>button>img{
                    width: 180px;
                    height: 100px;
                }

                &>button {
                    display: flex;
                    background: none;
                    color: var(--CorText);

                    justify-content: center;
                    align-items: flex-end;

                    &>img {
                        position: absolute;
                        top: 0;
                        left: 0;

                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        z-index: -1;
                    }
                }
            }

        }
    }
}